<template>

  <v-header title="корзина" />

	<div class="cart">
   <div class="container">
     <div class="card-box">
       <card
          v-for="meal in meals" :key="meal.id"
          :meal="meal"
          @openPopup="openPopup"
        />
      </div>

      <h3 class="tag-title">ваш комментарий</h3>
      <div class="textarea-box">
        <textarea v-model="customerComment" name="comment" class="form-control form-control--area" placeholder="Если у вас есть дополнительные пожелания, пожалуйста, дайте нам знать"></textarea>
      </div>

      <h3 class="tag-title" id="payMethod">варианты оплаты</h3>
      <div class="form-box">
        <label class="form-control form-control--radio">
          <p>Закрыть на номер</p>
          <input
            class="form-control"
            type="radio"
            name="pay"
            v-model="payMethod"
            :value="1"
          >
        </label>
         <label class="form-control form-control--radio">
          <p>Наличными </p>
          <input
            class="form-control"
            type="radio"
            name="pay"
            v-model="payMethod"
            :value="2"
          >
        </label>
        <label class="form-control form-control--radio">
          <p>Картой при получении</p>
          <input
            class="form-control"
            type="radio"
            name="pay"
            v-model="payMethod"
            :value="3"
          >
        </label>
         <label class="form-control form-control--radio">
          <p>Онлайн-оплата картой</p>
          <input
            class="form-control"
            type="radio"
            name="pay"
            v-model="payMethod"
            :value="4"
          >
        </label>
      </div>

      <h3 class="tag-title" id="typeDelivery">тип доставки</h3>
      <div class="form-box">
        <label class="form-control form-control--radio">
          <p>Принести в номер</p>
          <input class="form-control" type="radio" v-model="typeDelivery" value="room" name="delivery">
        </label>
        <label class="form-control form-control--radio">
          <p>Забрать в ресторане</p>
          <input class="form-control" type="radio" v-model="typeDelivery" value="restauran" name="delivery">
        </label>
      </div>

      <div class="total-price">
        <div class="total-price__text">
          <p>Общая стоимость:</p> <span>{{totalyPrice}} ₽</span>
        </div>
        <button @click="order" class="btn">Заказать</button>
      </div>

    </div> 
  </div>

  <popup :show="popupShow" :meal="meal" @closePopup="closePopup" @showSnack="showSnack"/>

  <v-snack-bar
    :show="snackbarShow"
    :config="snacks[snack]"
  />
</template>

<script>
import Card from "@/components/Card"
import axios from "axios"
import Popup from "@/components/Popup"
import VHeader from "@/components/VHeader"
import VSnackBar from "@/components/VSnackBar"


export default {

  name: 'Cart',

  components: {
    Popup,
    Card,
    VHeader,
    VSnackBar
  },

  inject: {
     store: '$store'
  },

  created() {
    this.meals = this.store.getters.getAllCart
  },

  data () {
    return {
      popupShow: false,
      cartCount: 0,

      customerComment: "",
      payMethod: '',
      typeDelivery: '',
      meal: {},

      meals: [],

      snackbarShow: false,
      snack: 0,

      snacks: [
        {
          text: "Выберите основную опцию",
          modificator: "alert"
        },
        {
          text: "Добавлено в корзину!",
          modificator: ""
        },
        {
          text: "Выберите количество",
          modificator: "alert"
        },
      ],
    }
  },

  computed: {
    totalyPrice() {
      return this.store.getters.getCartPrice
    }
  },

  methods: {
    openPopup(id) {
      this.meal = this.meals.find(meal => meal.id == id)
      this.popupShow = true
    },
    closePopup(popupShow) {
      this.popupShow = popupShow
    },

    showSnack(snack) {
      //chosen snack
      this.snack = snack

      this.snackbarShow = true
      setTimeout(()=> this.snackbarShow = false, 2000)
    },

    order() {
      // formating data
      const orderBody = {
        items: [],
        customer_comment: this.customerComment
      }

      for(const meal of this.meals) {
        orderBody.items.push({
          item: meal.id.toString(),
          quantity: meal.quantity
        })
      }

      // scroll to unchecked
      const typeDeliveryEl = document.querySelector("#typeDelivery"),
            typeDeliveryY = typeDeliveryEl.offsetTop,
            typeDeliveryX = typeDeliveryEl.offsetWidth;

      if (!this.typeDelivery || !this.payMethod) {
        window.scrollTo({
          top: typeDeliveryY,
          left: typeDeliveryX,
          behavior: 'smooth'
        })
      } else if ( this.totalyPrice <= 0 ) {
        alert("Вы не выбрали товар!")
        return
      } else {
        this.$load(async ()=> {
          await this.$api.order.order(orderBody)
            .then(res => {
              this.status = res.status
              this.$router.push({name: "order", query: {
                status: res.status,
                orderId: res.data.id,
                paymentId: this.payMethod,
              } })
            })
            .catch(err => {
              if (err.response?.status == "409") {
                let blockedItems = err.response.data.items

                alert(blockedItems.length == 1
                  ? `Товары ${blockedItems.map(item => item.item)} уже недоступны`
                  : `Товар ${blockedItems[0].item} уже недоступен`
                )                
              }
              return
            })
        })
      }
    }
  }
}
</script>

<style lang="sass">
.tag-title
  display: block
  margin-left: 15px
</style>