import axios from 'axios'

export default {
	state: {
		cart: [],
		cartQuantity: 0,
		cartPrice: 0,
	},
	mutations: {
		setCart(state, payload) {
			state.cart = payload
		},
		updateCart(state, [todo, price]) {
			if (todo == 'add') {
				state.cartQuantity++
				state.cartPrice += parseInt(price)
			} else {
				state.cartQuantity--
				state.cartPrice -= price
			}

			// window.localStorage.setItem("cart", JSON.stringify(state.cart))
		},
		addCart(state, [meal, mealCount]) {
			if (state.cart.indexOf(meal) == -1) {
				state.cart.push(meal)
				state.cartPrice += parseInt(meal.price)
			}

			state.cartQuantity += mealCount
			// window.localStorage.setItem("cart", JSON.stringify(state.cart))
		}
	},
	getters: {
		getAllCart(state) {
			return state.cart
		},
		getCartQuantity(state) {
			return state.cartQuantity
		},
		getCartPrice(state) {
			return state.cartPrice
		}
	},
	actions: {
	},
}
