<template>
	<v-header title="статус заказа" order />

	<!-- order -->
	<div class="order">
		<div class="container">
			<div class="order-status" :class="!success ? 'order-status--non' : ''">
				<div v-if="success">
					<img class="order-status__icon" src="@/assets/icons/ok.svg" alt="">
						
					<h2 class="order-status__text">Спасибо за ваш заказ, менеджер скоро свяжется с Вами!</h2>
				</div>
				<div v-else>
					<img class="order-status__icon" src="@/assets/icons/non-ok.svg" alt="">

					<h2 class="order-status__text">Оплата не прошла</h2>
				</div>
				

				<button v-if="!success" class="btn btn--grey">попробовать еще раз</button>
			</div>

			<div v-if="success" class="order__receipt">
				<div class="order__price">
					<span>Сумма к оплате:</span> <p>{{totalyPrice}} ₽</p>
				</div>
				<div class="order__pay">
					<span>Оплачен</span> <p>{{payed}}</p>
				</div>

				<div v-if="this.meals.length" class="order__list-box">
					<span>Состав заказа</span>
					<ul class="order__list">
						<li v-for="meal of this.meals" :key="meal.id" class="order__item">
							<p>{{meal.name}}</p>
							<span>{{meal.quantity}} шт.</span>
						</li>
					</ul>
				</div>

				<button class="btn btn--grey">скачать квитанцию</button>
			</div>
		</div>	
	</div>
	<!-- ./order -->

	<router-link to="/"><button class="btn footer-btn">вернуться на главную</button></router-link>

</template>

<script>
import VHeader from "@/components/VHeader"

export default {

	name: 'Order',

	components: {
		VHeader
	},

	inject: {
		store: '$store'
	},

	created () {
		this.meals = this.store.getters.getAllCart

		const windowData = Object.fromEntries(
      new URL(window.location).searchParams.entries()
    );

    this.success = windowData?.status == "200" ? true : false
    this.orderId = windowData?.orderId
    this.paymentId = windowData?.paymentId

    this.$load(async ()=> {
      await this.$api.pay.pay(this.orderId, this.paymentId)
        .then(res => {
          console.log(res.data)
        })
        .catch(err => {
          console.log("err: ", err)
        })
      })
	},

	computed: {
    totalyPrice() {
      return this.store.getters.getCartPrice
    }
  },

	data () {
		return {
			success: false,
			payed: "Нет",

			meals: []
		}
	}
}
</script>

