<template>
	<v-header>
		<div class="menu-type" @click="typeShow ? typeShow = false : typeShow = true">
			<p>{{typeText}}</p>

			<svg width="12" height="8" viewBox="0 0 12 8" fill="none" xmlns="http://www.w3.org/2000/svg"
				:class="typeShow ? '' : 'caret-active' "
			>
				<path d="M12 6.37061L10.3421 8L5.99701 3.39356L1.65785 8L-2.78468e-07 6.37061L5.17107 0.882085L5.99701 -2.62399e-07L6.82892 0.882085L12 6.37061Z" fill="#ACACCA"/>
			</svg>

		</div>

		<ul v-if="typeShow" class="menu-type__list">
			<li 
				@click="selType" 
				class="menu-type__item"
				:class="typeText.toLowerCase() == 'меню' ? 'menu-type__item--active' : ''"
			>
				Меню
			</li>

			<li 
				@click="selType" 
				class="menu-type__item"
				:class="typeText.toLowerCase() == 'акции' ? 'menu-type__item--active' : ''"
			>
				акции
			</li
			>
			<li 
				@click="selType" 
				class="menu-type__item"
				:class="typeText.toLowerCase() == 'комбо' ? 'menu-type__item--active' : ''"
			>
				комбо
			</li
			>
		</ul>
	</v-header>
	<!-- menu -->
	<div class="menu">
		<div class="container container--menu">
			<ul @click="selMeals" class="swipe-menu__list">
				<li
					v-for="category in this.categories" :key="category.id"
					@click="changeCategory"
					class="swipe-menu__item"
				>
					<a :href="'#'+category.name">{{category.name}}</a>
				</li>
			</ul>

			<div
				v-for="category in this.categories" :key="category.id"
				v-if="typeText.toLowerCase() == 'меню'"
				class="menu__card"
			>
				<h3
					v-if="currentMeals(meals, category.id).length"
					class="tag-title"
					:id="category.name"
				>
					{{category.name}}
				</h3>

				<div class="card-box">
					<card
						v-for="meal in currentMeals(meals, category.id)" :key="meal.id"
						:meal="meal"
						@openPopup="openPopup"
						
					/>
				</div>
			</div>

			<div class="total-price">
				<div class="total-price__text">
					<p @click="snackbarShow = !snackbarShow">Общая стоимость:</p> <span>{{totalyPrice}} ₽</span>
				</div>
				<router-link to="cart" class="full-w"><button class="btn">перейти в корзину</button></router-link>
			</div>

		</div>
	</div>
	<!-- ./menu -->

	<popup :show="popupShow" :meal="meal" @closePopup="closePopup" @showSnack="showSnack"/>

	<v-snack-bar
		:show="snackbarShow"
		:config="snacks[snack]"
	/>


</template>

<script>
import Popup from "@/components/Popup"
import Card from "@/components/Card"
import VSnackBar from "@/components/VSnackBar"
import VHeader from "@/components/VHeader"

export default {

	name: 'Menu',

	components: {
		Popup,
		Card,
		VHeader,
		VSnackBar
	},

	inject: {
		 store: '$store'
	},

	created() {
		this.store.dispatch('fetchMeals')
	},

	data () {
		return {
			typeShow: false,
			typeText: "Меню",
			popupShow: false,
			snackbarShow: false,
			snack: 0,

			snacks: [
				{
					text: "Выберите основную опцию",
					modificator: "alert"
				},
				{
					text: "Добавлено в корзину!",
					modificator: ""
				},
				{
					text: "Выберите количество",
					modificator: "alert"
				},
			],


			meal: {},
		}
	},

	computed: {
		totalyPrice() {
			return this.store.getters.getCartPrice
		},
		meals() {
			return this.store.state.mealsModule.meals.products
		},
		categories() {
			return Object.values(this.store.state.mealsModule.meals)[0]
		},
	},

	methods: {
		showSnack(snack) {
			//chosen snack
			this.snack = snack

			this.snackbarShow = true
			setTimeout(()=> this.snackbarShow = false, 2000)
		},

		changeCategory(event) {
			const swipeMenuItems = document.querySelectorAll(".swipe-menu__item")

			for( let otherSwipeMenuItem of swipeMenuItems ) {
				otherSwipeMenuItem.classList.remove('swipe-menu__item--active')
			}
			event.currentTarget.classList.add('swipe-menu__item--active')
		},

		currentMeals(meals, caterogyId) {
			return meals.filter( meal => meal.categoryId == caterogyId)
		},

		openPopup(id) {
			this.meal = this.meals.find(meal => meal.id == id)
			this.popupShow = true
		},
		closePopup(popupShow) {
			this.popupShow = popupShow
		},

		selType(event) {
			const currentItem = event.currentTarget
			for( let menuTypeItem of document.querySelectorAll(".menu-type__items") ) {
				menuTypeItem.classList.remove("menu-type__item--active")
			}
			
			this.typeText = currentItem.innerText
			currentItem.classList.add("menu-type__item--active")

			this.typeShow = false
		}
	},
}
</script>

