<template>
    <!-- header -->
    <header class="header">
      <div class="header__wrap">
        <router-link to="/" class="header__logo">Room Service</router-link>

        <lang-switcher />
      </div>
    </header>
    <!-- ./header -->

    <!-- login content -->
    <div class="login">
      <div class="container">
        <div class="login__wrap">
          <div class="login__header">
            <img  src="@/assets/icons/logo.svg" alt="Логотип" class="login__icon">

            <h2 class="login__text">Пожалуйста, укажите <br>номер телефона и номер комнаты.</h2>
          </div>

          <form class="form" action="#" @submit.prevent>
            <input
              class="form-control"
              placeholder="Номер телефона"
              v-maska="['+7 (###) ##-##-##', '+7 (###) ###-##-##']"
              @maska="phone = '7' + $event.target.dataset.maskRawValue"
              type="text"
            >
            <input
              class="form-control"
              placeholder="Код бронирования"
              v-model="booking_id"
              type="text"
            >
            <label
              v-if="Boolean(booking_id) || Boolean(phone)"
              class="form-control form-control--checkbox"
              >
              <input type="checkbox" v-model="checked">
              <p>Я согласен с <router-link to="user_agreement">пользовательским соглашением</router-link></p>
            </label>

            <button
              class="btn "
              :disabled='!checked'
              @click="recaptcha"
              >войти</button>
          </form>
        </div>
      </div>
    </div>
    <!-- ./login content -->

    <!-- footer -->
    <footer class="footer">
      <div class="container">
        <div class="footer__wrap">
          <span class="footer__text">
            Если у вас есть вопрос другого <br>характера, просим <a href="#">связаться с поддержкой</a>
          </span>

          <img src="@/assets/icons/footer-icon.svg" alt="">
        </div>
      </div>
    </footer>
    <!-- ./footer -->

  <popup :show="popupShow" class="sms" @closePopup="closePopup" timer/>
</template>

<script>
import Popup from "@/components/Popup"
import LangSwitcher from "@/components/LangSwitcher"

export default {
  name: 'Login',

  components: {
    Popup,
    LangSwitcher
  },

  inject: {
    store: "$store"
  },

  created() {
    this.hash = this.$route.params?.hash
    window.localStorage.removeItem("user")
  },

  data() {
    return {
      phone: "",
      booking_id: "",
      qr: "achhjsiGxIMWuXWj8Qpx4zXLKJAzT2EXlGbNfxMzu8clU4dDQ96OTwru3RjeRXEcQcNDYBRt",
      captcha_code: '',

      checked: false,
      popupShow: false,
      hash: ''
    }
  },

  methods: {
    closePopup(closePopup) {
      this.popupShow = closePopup
    },

    login() {
      const body = {
        phone: this.phone,
        booking_id: this.booking_id,
        qr: this.qr,
        captcha_code: this.captcha_code,
      }

      this.$load(async () => {
        const data = await this.$api.auth.signIn(body)
          .then(res => {
            this.$router.push('/menu')
            this.popupShow = true

            this.store.commit("setUser", res.data)
            localStorage.setItem('user', JSON.stringify(res.data))
          })
          .catch(err => {
            if (err.response.status == "403") {
              alert("Неправильный телефон или каптча")
            } else if (err.response.status == "400") {
              alert("Вы не ввели телефон")
            } else if (err.response.status == "429") {
              alert("Подождите отправки данных")
            } else if (err.response.status == "410") {
              alert("Смс устарел")
            } else if (err.response.status == "425") {
              alert("Смс ещё не истёк")
            } else if (err.response.status == "500") {
              alert("Технические проблемы сервера")
            }
          })
      })
    },

    recaptcha() {
      this.$recaptchaLoaded().then(() => {
        this.$recaptcha("login").then((token) => {
          this.captcha_code = token;
          this.login()
        });
      });
    },
  }
}
</script>

