<template>
	<div class="card" :class="meal.priceProm ? 'card--prom' : ''">
		<div class="card__info" @click="openPopup">
			<div
				v-if="meal?.label == 'vegan'"
				class="card__label card__label--vegan"
				style="position: absolute;"
			>
				<img src="@/assets/icons/vegan.svg" alt="">
			</div>
			<div
				v-if="meal?.label == 'hot'"
				class="card__label card__label--hot"
				style="position: absolute;"
			>
				<img src="@/assets/icons/hot.svg" alt="">
			</div>
			<div
				v-if="meal?.label == 'prom'"
				class="card__label card__label--prom"
				style="position: absolute;"
			>
				<img src="@/assets/icons/prom.svg" alt="">
			</div>
			<div
				v-if="meal?.label == 'diet'"
				class="card__label card__label--diet"
				style="position: absolute;"
			>
				<img src="@/assets/icons/diet.svg" alt="">
			</div>

			<img src="@/assets/card-item.jpg" alt="">
			<div class="card__text">
				<h2 class="card__title">{{meal.name}}</h2>
				<span v-if="meal.weight" class="card__weight">{{meal.weight}}г</span>
			</div>
		</div>

		<div class="card__action">
			<p v-if="!meal.priceProm" class="card__price">{{meal.price}} ₽</p>
			<div v-else class="card-box--prom">
				<p class="card__price card__price--prom">{{meal.priceProm}} ₽</p>
				<p class="card__price">{{meal.price}} ₽</p>
			</div>

			<div class="btn-box">
				<button @click="updateCart(meal, 'substract')" class="btn btn--card">-</button>
				<button @click="updateCart(meal, 'add')" class="btn btn--card">+</button>
			</div>

			<span v-if="mealInCart?.quantity" class="count-meal">{{mealInCart?.quantity}} шт.</span>
		</div>
	</div>
</template>

<script>
export default {

	name: 'Card',

	inject: {
		store: '$store'
	},
	
	data () {
		return {
      cartMeals: this.store.getters.getAllCart,
		}
	},

	props: {
		meal: {
			type: Object
		}
	},

	computed: {
		mealInCart() {
			return this.store.getters.getAllCart.find(cartMeal => cartMeal.id == this.meal.id)
		},
		ingredientsGroups() {
			return this.store.state.mealsModule.meals.ingredientsSchemes
							.find(scheme => scheme.id == this.meal.schemeId)?.ingredientsGroups
		},
		required() {
			return this.ingredientsGroups?.some(ingredientsGroup => ingredientsGroup.minCount == 1) ? true : false
		}
	},

	methods: {
		openPopup() {
			this.$emit('openPopup', this.meal.id)
		},

		updateCart(meal, todo) {
			// styling
			const target = event.currentTarget
			target.classList.add("active")

			setTimeout(event=> {
				target.classList.remove("active")
			}, 200)

			// logic
			if (this.required) {
				this.openPopup()
				return
			}

      let cartMealExist = this.cartMeals.find(cartMeal => cartMeal.id == meal.id)
      
      if (cartMealExist) {
        if (todo == "substract") {
          if (meal.quantity > 0) {
            meal.quantity--
          }
          if (meal.quantity == 0) {
            let indexMeal = this.cartMeals.findIndex(cartMeal => cartMeal.quantity == 0)
            this.cartMeals.splice(indexMeal, 1)
          }

          this.store.commit('updateCart', [todo, meal.price])
        } else {
          this.store.commit('updateCart', [todo, meal.price])
          this.meal.quantity++
        }
      } else if (todo == "add") {
				this.store.commit('updateCart', [todo, meal.price])
        meal.quantity = this.meal.quantity = 1
        this.cartMeals.push(meal)
      }
      this.store.commit('setCart', this.cartMeals)
    },
	}
}
</script>

