<script>
export default {

	name: 'Popup',

	inject: {
		store: "$store"
	},

	data() {
		return {
			mealCount: 0,

			time: 59,
			timeInterval: null,

			snack: 0,

			configPopUp: {
				single: false,
				multiply: false,
			},

			ingredients: [

			],
		}
	},

	props: {
		show: {
			require: true,
			type: Boolean
		},
		meal: {
			default: false,
		},
		timer: {
			type: Boolean,
			default: false,
		},
	},

	computed: {
		allIngredients() {
			return this.store.state.mealsModule.meals.ingredients
		},
		currentScheme() {
			return this.store.state.mealsModule.meals.ingredientsSchemes
							.find(scheme => scheme.id == this.meal.schemeId)
		},
		currentGroups() {
			let groups = []; // current groups

			this.store.state.mealsModule.meals.ingredientsGroups
				?.filter(ingredientsGroup => {

					this.currentScheme?.ingredientsGroups.forEach(currentGroup => {
						if (currentGroup.id == ingredientsGroup.id) {
							if (currentGroup.maxCount == 1) {
								ingredientsGroup.single = true
								ingredientsGroup.model = ""

							} else {
								ingredientsGroup.multiply = true
								ingredientsGroup.model = Array(ingredientsGroup.ingredients.length)
							}

							if (currentGroup.minCount == 1) {
								ingredientsGroup.require = true
							} else {
								ingredientsGroup.require = false
							}

							groups.push(ingredientsGroup)
						}
					})
				})

			return groups
		},
	},

	methods: {
	 closePopup() {
		 this.$emit("closePopup", false)
	 },

		updateCart(todo) {
			// styling
			const target = event.currentTarget
			target.classList.add("active")

			setTimeout(event=> {
				target.classList.remove("active")
			}, 200)

			
			if (todo == "substract") {
				this.mealCount--

				if (this.mealCount <= 0) {
					this.mealCount = 0
				}

			} else {
				 this.mealCount++
			}
		},

		submitCart(event) {
			let requireGroups = this.currentGroups.filter(item => item.require)

			if (!requireGroups.every(currentGroup => Boolean(currentGroup.model)) ) {
				const ingredientMains = document.querySelectorAll(".ingredient__main")
				for (const ingredientMain of ingredientMains) {
					ingredientMain.classList.add("alert")
				}

				setTimeout(()=> ingredientMains.forEach(item => item.classList.remove("alert")), 2500)

				this.snack = 0
				
			} else if (this.mealCount == 0){
				const addCartBox = document.querySelector(".add-cart-box")
				addCartBox.classList.add("alert")

				setTimeout(()=> addCartBox.classList.remove("alert"), 2500)

				this.snack = 2

			} else {
				this.$emit("closePopup", false)
				this.meal.quantity = 0
				this.meal.quantity += this.mealCount
				this.meal.ingredients = []

				this.currentGroups.forEach(item => {

					if (typeof item.model == "string") {
						this.meal.ingredients.push({item: item.model, quantity: 1})
					} else if(typeof item.model == "object") {

						item.model.forEach((model, index) => {
							if (model) {
								this.meal.ingredients.push({item: item.ingredients[index], quantity: 1})
							}
						})
					}
				})

				this.snack = 1
				
				this.store.commit('addCart', [this.meal, this.mealCount])
			}

			this.$emit("showSnack", this.snack)
		},

		getIngredients() {
			this.ingredients = []
			this.currentGroups.forEach(currentGroup=>{
				this.allIngredients.filter(ingredient => {
					if (currentGroup.ingredients.includes(ingredient.id)) {
						this.ingredients.push(ingredient)
					}
				})
			})
		},

		currentIngredient(ingredient) {
			return this.ingredients.find(item => item.id == ingredient)
		}
	},

	watch: {
		show() {
			if (this.timer) {
				clearInterval(this.timeInterval)
				this.timeInterval = setInterval(() => this.time--, 1000)
			}
			
			this.getIngredients()

			if(this.show && this.meal){
				document.body.style.overflow = 'hidden'
			} else {
				document.body.style.overflow = 'auto'
			}
		},

		time() {
			if (this.time == 0 && this.show && this.show) {
				clearInterval(this.timeInterval)
			}
		},
	}
}
</script>

